$bg: red;

body {
    background-color: $bg;
}

.container {
    padding: 1rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background: -webkit-gradient(linear, left bottom, left top, from(black), to(white));
    background: linear-gradient(to top, black, white);
    h1 {
        font-size: 68px;
        text-align: center;
    }
}