$_navBlack: #1b1b1b;
$_dropdownBGColor: lighten($_navBlack, 10%);

nav {
  position: sticky;
  top: 0;
  z-index: 10;
  background-color: $_navBlack;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /*transition for nav open/close animation on mobiles*/
  -webkit-transition: all 400ms;
  transition: all 400ms;
  .nav-list {
    grid-column-start: 1;
    grid-column-end: 12;
    list-style: none;
    margin: 0;
    padding: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: center;
    .nav-list-item {
      width: 100%;
      .nav-list-item-anchor {
        display: block;
        color: white;
        text-decoration: none;
        padding: 0.5em;
        &.active, &:hover, &.nav-dropdown-show {
          background-color: $_dropdownBGColor;
        }
      }
      .nav-dropdown-name {
        display: block;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
      }
      .nav-dropdown-toggle {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
      }
      .nav-dropdown-toggle-caret {
        margin-left: 0.5em;
      }
      .nav-dropdown {
        height: 0;

        background-color: $_dropdownBGColor;
        -webkit-transition: opacity 200ms;
        transition: opacity 200ms;
        display: none;
        &.dropdown-show {
          height: auto;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-orient: vertical;
          -webkit-box-direction: normal;
              -ms-flex-direction: column;
                  flex-direction: column;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
        }
      }
      .nav-dropdown a {
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        padding: 0.5em 0;
        color: white;
        text-decoration: none;
        white-space: nowrap;
        width: 100%;
        &:hover {
          background-color: lighten($_dropdownBGColor, 10%);
        }
      }
    }
    .nav-list-item-main > .nav-menu-toggle {
      display: block;
      background-color: transparent;
      border: 0;
      padding: 1em;
      &:hover {
        background-color: $_dropdownBGColor;
      }
    }
    .nav-main {
      background-color: $_navBlack;
      height: 0;
      opacity: 0;
      overflow: hidden;
      &.menu-open {
        height: auto;
        opacity: 1;
      }
    }
    & > .nav-list-item.nav-list-item-main {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
    }
  }
}
@media screen and (min-width: 768px) {
  nav {
    .nav-list {
      grid-column-start: 2;
      grid-column-end: 14;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      .nav-list-item {
        width: auto;
        &.nav-list-item-dropdown {
          position: relative;
        }
        .nav-dropdown {
          position: absolute;
          z-index: 10;
          top: 100%;
          left: 0;
          -webkit-box-align: start;
              -ms-flex-align: start;
                  align-items: start;
          padding: 0.4em 0;
        }
        .nav-dropdown a {
          -webkit-box-sizing: border-box;
                  box-sizing: border-box;
          width: 100%;
          text-align: left;
          padding: 0.2em 1em;
        }
      }
      .nav-list-item.nav-list-item-main > .nav-menu-toggle {
        display: none;
      }
      .nav-main {
        height: auto;
        padding: 0;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        position: static;
        opacity: 1;
        overflow: visible;
        .nav-list .nav-list-item {
          margin-right: 1em;
        }
        .nav-main-menu {

        }
      }
    }
  }
}
